.popup {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  color: #000;
}

.popup__container {
  position: relative;
  display: flex;

  flex-direction: column;
}

.popup__container_type_form {
  background-color: #fff;
  padding: 34px 36px;
  border-radius: 20px;
  width: 358px;
}

.popup__btn-close {
  position: absolute;
  top: -36px;
  right: -36px;
  cursor: pointer;
  background: transparent;
  border: none;
  padding: 0;
}

.popup__btn-close:hover {
  opacity: 1;
}

.popup__btn-close-img {
  width: 40px;
  height: 40px;
  fill: #000;
}

.popup__title {
  color: #1a1b22;
  font-family: 'Roboto', sans-serif;
  font-size: 24px;
  font-weight: 900;
}

.popup__form {
  display: flex;
  flex-direction: column;
  margin-top: 8px;
}

.popup__legend {
  color: #000;
  margin: 12px 0;
}

.popup__secondary-btn-box {
  display: flex;
  justify-content: center;
  gap: 6px;
}

.popup__btn_secondary {
  border: none;
  background: none;
  color: #2f71e5;
  font-family: 'Inter', sans-serif;
  font-size: 14px;
  cursor: pointer;
}

.popup__btn-submit {
  display: inline-flex;
  padding: 20px 0;
  justify-content: center;
  align-items: center;
  border-radius: 100px;
  background: #2f71e5;
  border: none;
  box-shadow: 0px 5px 15px 0px rgba(14, 26, 57, 0.2);
  cursor: pointer;
  color: #fff;

  /* button */
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  font-weight: 500;
  line-height: 24px;
}

.popup__buttons {
  margin-top: 32px;
  display: flex;
  gap: 16px;
  flex-direction: column;
}

.popup__btn-submit:hover:not(:disabled) {
  background: #347eff;
}

.popup__btn-submit:active {
  background: #2a65cc;
}

.popup__btn-submit:disabled {
  background: #e6e8eb;
  color: #b6bcbf;
}

.popup__btn-submit_disabled {
  opacity: 0.3;
}

.popup__fieldset {
  display: flex;
  flex-direction: column;
  gap: 8px;
  border: none;
  margin: 0;
  padding: 0;
}

.popup__fieldset-label {
  margin-left: 12px;
}

@media screen and (max-width: 400px) {
  .popup {
    align-items: flex-end;
  }
  .popup__container {
    height: calc(100vh - 86px);
    width: 100vw;
    padding: 16px;
    border-radius: 20px 20px 0 0;
  }

  .popup__btn-close {
    top: -38px;
    right: 16px;
  }

  .popup__btn-close-img {
    width: 24px;
    height: 24px;
  }
}
