.search-results {
  background: #f5f6f7;
  min-height: 282px;
  position: relative;
}

.search-results__container {
  padding: 80px 40px;
  max-width: 1232px;
  margin: auto;
  display: flex;
  flex-direction: column;
  gap: 64px;
  align-items: center;
}

.search-results__title {
  align-self: flex-start;
  /* font */
  color: #1a1b22;
  font-family: 'Roboto Slab', sans-serif;
  font-size: 40px;
  font-weight: 400;
  line-height: 46px; /* 115% */
}

.search-results__button {
  align-self: center;
  border-radius: 80px;
  background: var(--main-white, #fff);
  width: 288px;
  height: 64px;
  border: none;
  cursor: pointer;
  transition: all linear 0.1s;
  opacity: 1;
  /* font */
  color: var(--main-primary, #1a1b22);
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  font-weight: 500;
  line-height: 24px; /* 133.333% */
}

.search-results__button:hover {
  border-radius: 80px;
  opacity: 0.9;
  background: #e8e8e8;
}

@media screen and (max-width: 900px) {
  .search-results__container {
    padding: 40px;
    gap: 32px;
  }
  .search-results__button {
    height: 56px;
    width: 240px;
  }
  .search-results__title {
    font-size: 30px;
    line-height: 34px; /* 113.333% */
  }
}

@media screen and (max-width: 400px) {
  .search-results__title {
    margin-bottom: 58px;
  }
  .search-results__container {
    padding: 32px 16px 24px;
    gap: 0;
  }

  .search-results__button {
    width: 100%;
    margin-top: 21px;
  }
}
